<template>
    <header :class="{'scrolled-nav' : scrollPosition }">
        <nav>
            <div class="branding">
                <img 
                    src="https://th.bing.com/th/id/R.28c748889e8d70a00c85eca5679fca90?rik=rjcyIjWRXXC0Wg&pid=ImgRaw&r=0" alt="">
            </div>
            <ul v-show="!mobile" class="navigation">
                <li><router-link class="link" to="/">Inicio</router-link></li>
                <li><router-link class="link" to="/agentes">Agentes</router-link></li>
                <li><router-link class="link" to="/mapas">Mapas</router-link></li>
                <li><router-link class="link" to="/armas">Armas</router-link></li>
            </ul>
            <div class="icon">
                <i 
                    @click="toggleMobileNav" 
                    v-show="mobile" 
                    class="far fa-bars" 
                    :class="{'icon-active' : mobileNav}"></i>
            </div>
            <transition name="mobile-nav">
               <ul v-show="mobileNav" class="dropdown-nav">
                    <li><router-link @click="handleClick" class="link" to="/">Inicio</router-link></li>
                    <li><router-link @click="handleClick" class="link" to="/agentes">Agentes</router-link></li>
                    <li><router-link @click="handleClick" class="link" to="/mapas">Mapas</router-link></li>
                    <li><router-link @click="handleClick" class="link" to="/armas">Armas</router-link></li>
                </ul>  
            </transition>
        </nav>
    </header>
</template>
<script>
export default {
    name:"NavBar",
    data(){
        return{
            scrollPosition:null,
            mobile:null,
            mobileNav:null,
            windowWidth:null
        }
    },
    created(){
        window.addEventListener("resize",this.checkScreen);
        this.checkScreen();
    },
    mounted(){
        window.addEventListener("scroll",this.updateScroll);
    },
    methods:{
        toggleMobileNav(){
            this.mobileNav = !this.mobileNav;
        },
        updateScroll(){
            const scrollPosition = window.scrollY;
            if(scrollPosition > 150) {
                this.scrollNav = true;
                return;
            }
            this.scrollNav = false;
        },
        checkScreen(){
            this.windowWidth = window.innerWidth;
            if ( this.windowWidth <=750){
                this.mobile = true;
                return;
            }
            this.mobile = false;
            this.mobileNav = false;
            return;
        },
        handleClick(){
            this.mobileNav = !this.mobileNav;
        }
    }
}
</script>
